.VideoBox> * {
    margin: 12px 0 0 12px;
    -moz-box-shadow: .2em .2em .2em #333;
    -webkit-box-shadow: .2em .2em .2em #333;
    box-shadow: .2em .2em .2em #333;
    
  }
.VideoBox {
    display: inline-flex;
    flex-wrap: wrap;
    margin: -12px 0 0 -12px;
    width: calc(100% + 10px);
    margin-bottom: 19.5%; 


}
